        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">ÎåìÍ∏Ä ÏàòÏ†ï</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <form>
            <div class="mb-3">
              <label  class="form-label" >ÎãâÎÑ§ÏûÑ</label>
              <input
                type="text"
                class="form-control form-control-sm"
                id="edit-comment-nickname"
              />
            </div>
            <div class="mb-3">
              <label class="form-label">ÎåìÍ∏Ä ÎÇ¥Ïö©</label>
              <textarea
                type="text"
                class="form-control form-control-sm"
