<script>
import { Modal } from "bootstrap";
import axios from "axios";

export default {
  name: "EditCommentModal",
  methods: {
    updateComment() {
      const id = document.querySelector("#edit-comment-id").value;
      const content = document.querySelector("#edit-comment-body").value;
      const secret = false; // ÏÇ¨Ïö©Ïûê ÏûÖÎ†• Í∞íÏúºÎ°ú Î≥ÄÍ≤ΩÌï¥Ïïº Ìï©ÎãàÎã§.

      const boardId = this.$route.params.id; // Í≤åÏãúÍ∏Ä ID Ï∂îÏ∂ú
       const path = this.$route.path;
       let baseUrl ;

      if(path.includes("reforme"))
          baseUrl = `/reforme/board/${boardId}/comment`;
      else if(path.includes("reforyou"))
          baseUrl = `/reforme/board/${boardId}/comment`;
      const commentDto = { content , secret }
      
      
      const headers = {
        "Content-Type": "application/json", // JSON ÌòïÏãùÏúºÎ°ú Îç∞Ïù¥ÌÑ∞Î•º Ï†ÑÏÜ°Ìï¥Ïïº Ìï©ÎãàÎã§.
        "Authorization": `Bearer ${localStorage.getItem('accessToken')}` // Ïù∏Ï¶ù ÌÜ†ÌÅ∞ÏùÑ Ìó§ÎçîÏóê Ï∂îÍ∞ÄÌï©ÎãàÎã§.
      };

      axios.patch(`${baseUrl}?id=${id}`, commentDto, { headers }) // ÏöîÏ≤≠ URLÏóê ÏøºÎ¶¨ Ïä§Ìä∏ÎßÅÏùÑ Ìè¨Ìï®Ìï©ÎãàÎã§.
        .then(response => {
          const msg = (response.data.statusCode === 200) ? "ÎåìÍ∏ÄÏù¥ ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§." : "ÎåìÍ∏Ä ÏàòÏ†ï Ïã§Ìå®";
          alert(msg);

          
            

              if(path.includes("reforme"))
                this.$router.push("/reforme_page");
              else if(path.includes("reforyou"))
                this.$router.push("/reforyou_page");
              
              this.$emit('update-comment', { id, content, secret });
            const modalElement = document.getElementById('comment-edit-modal');
            const modalInstance = Modal.getInstance(modalElement);
            modalInstance.hide();
          //baseUrl = `/reforme/board/${boardId}/comment`;

        })
        .catch(error => {
          console.error("ÎåìÍ∏Ä ÏàòÏ†ï Ïã§Ìå®:", error);
          alert("ÎåìÍ∏Ä ÏàòÏ†ïÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§: " + error.message);
        });
    }
  },
  mounted() {
    const commentEditModal = document.getElementById("comment-edit-modal");
    commentEditModal.addEventListener("show.bs.modal", (event) => {
      const triggerBtn = event.relatedTarget;
      const id = triggerBtn.getAttribute("data-bs-id");
      const nickname = triggerBtn.getAttribute("data-bs-nickname");
      const body = triggerBtn.getAttribute("data-bs-body");
      const articleId = triggerBtn.getAttribute("data-bs-article-id");

