<script>
//import writePost from "./writePost.vue";
//import postDetails from "./postDetails.vue";
import axios from "axios";

import emitter from "../store/eventBus"; // eventBus import Ï∂îÍ∞Ä


export default {
  name: "Reforme",
  components: {
   //writePost,
    //postDetails,
  },
  data() {
    return {
      step: 0,
      selectedFooterButton: "Î¶¨Ìè¨ÎØ∏",
      Í≤åÏãúÍ∏Ä: [],
      selectedPost: null,
      category: "ALL", // Í∏∞Î≥∏ Ïπ¥ÌÖåÍ≥†Î¶¨ ÏÑ§Ï†ï
    };
  },
  created() {
    this.$store.commit("setReforme", true);
