    this.fetchBoards();
    //const path = this.$route.path;
    this.emitter.emit("updateButtons", {
      menuButton: true,
      searchButton: true,
      backButton: false,
    });
  },
  mounted() {
    const path = this.$route.path;
    let reformeValue;
    if (path.includes("/reforyou_page")) {
      reformeValue = false;
    } else if (path.includes("/reforme_page")) {
      reformeValue = true;
    }

    // Vuex ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
    this.$store.dispatch("updateReforme", reformeValue);
    axios
      .get("/reforme/ALL")
      .then((response) => {
        if (response.data.statusCode === 200) {
          
          this.Í≤åÏãúÍ∏Ä = response.data.data;
          console.log(response.data.statusCode);
        } else {
          alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®");
        }
      })
      .catch((error) => {
        alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®: " + error.message);
      });

    this.emitter.on("backfunction", (data) => {
      this.step = data;
    });
  },
  updated() {
    this.$store.dispatch("updateReforme", true);
    this.emitter.emit("reforme_or_reforyou", {
      menuButton: true,
      searchButton: true,
      backButton: false,
    });
  },
  methods: {
    async fetchBoards() {
      try {
        const response = await axios.get(`/reforme/${this.category}`);
        if (response.data.statusCode === 200) {
          this.Í≤åÏãúÍ∏Ä = response.data.data;
        } else {
          alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®");
        }
      } catch (error) {
        alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®: " + error.message);
      }
    },
    async filterBoards(category) {
      this.category = category;
      await this.fetchBoards();
    },
    async searchBoards(searchWord) {
      try {
        const response = await axios.get(`/reforme/search/${searchWord}`);
        if (response.data.statusCode === 200) {
          this.Í≤åÏãúÍ∏Ä = response.data.data;
        } else {
          alert("Í≤ÄÏÉâ Í≤∞Í≥º Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®");
        }
      } catch (error) {
        alert("Í≤ÄÏÉâ Í≤∞Í≥º Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®: " + error.message);
      }
    },
    addPost(post) {
      this.Í≤åÏãúÍ∏Ä.push(post);
      this.step = 0;
    },
    selectFooterButton(button) {
      this.selectedFooterButton = button;
    },
    openPostDetails(post) {
      //this.selectedPost = post;
      //<router-link :to="`/detail/${selectedPost.boardId}`" :post="selectedPost"></router-link>
      this.$router
        .push({
          name: "reforme_detailPage",
          params: { id: Number(post.boardId) }, // Ïà´ÏûêÎ°ú Î≥ÄÌôò
        })
        .catch((err) => {
          console.error(err); // ÏóêÎü¨ Î°úÍ∑∏ Ï∂îÍ∞Ä
        });
      this.step = 3;
    },
    createPost() {
      this.selectedPost = null;
      this.$router
        .push({
          name: "write_page",
        })
        .catch((err) => {
          console.error(err); // ÏóêÎü¨ Î°úÍ∑∏ Ï∂îÍ∞Ä
        });
      this.step = 1;
    },
    // deletePost(postId) {
    //   this.Í≤åÏãúÍ∏Ä = this.Í≤åÏãúÍ∏Ä.filter((post) => post.id !== postId);
    //   this.step = 0;
    // },
    getFirstImage(images) {
      const image = images.find((image) => image.imagePath !== null);
      const imageUrl = image ? `${image.imagePath}` : "";
      console.log("Image URL:", imageUrl); // ÎîîÎ≤ÑÍπÖÏùÑ ÏúÑÌïú ÏΩòÏÜî Î°úÍ∑∏
      return imageUrl;
    },
    getCategoryName(category) {
      const categoryMap = {
        TOP: "ÏÉÅÏùò",
        OUTWEAR: "Ïô∏Ìà¨",
        BOTTOM: "ÌïòÏùò",
        BAG: "Í∞ÄÎ∞©",
        ETC: "Í∏∞ÌÉÄ",
      };
      return categoryMap[category] || "";
    },
  },

  beforeUnmount() {
    emitter.off("filterByCategory", this.filterBoards); // Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà Ìï¥Ï†ú
    emitter.off("search", this.searchBoards); // Í≤ÄÏÉâ Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà Ìï¥Ï†ú
  },
};
</script>

